export default {
	data() {
		return {
			content: null,
			isStuSignUp: false,
		}
	},
	onLoad(options) {
		var that = this;
		if (options) {
			that.content = options.content;
		}
	},
	methods: {
		navWhere(e) {
			e.currentTarget.dataset.key
			switch (e.currentTarget.dataset.key) {
				case 'home':
					uni.switchTab({
						url: '/pages/home/home'
					})
					break;
				case 'my':
					uni.switchTab({
						url: '/pages/myBusiness/myBusiness'
					})
					break;
				default:
					break;
			}
		}

	}
}
